<template>
  <div class="message">
    <van-nav-bar
      title="我的消息"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="box">
      <div
        class="cellbox"
        v-for="(item, i) in messagelist"
        :key="i"
        @click="displaybox(item)"
      >
        <van-swipe-cell>
          <van-card
            :desc="item.value"
            :title="item.title"
            class="goods-card"
            thumb="https://img.yzcdn.cn/vant/cat.jpeg"
          />
          <template #right>
            <van-button
              square
              text="删除"
              type="danger"
              class="delete-button"
              @click="remove(i)"
            />
          </template>
          <div class="textbox" v-show="item.isActive">
            <input type="text" class="inp" />
            <button class="btn">提交</button>
          </div>
        </van-swipe-cell>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      messagelist: [
        {
          title: "用户312312_sda",
          value: "欧泡时间到！！我要欧泡~",
          isActive: false,
        },
        {
          title: "用户da2312_131a",
          value: "斗罗大陆，我要继续追！",
          isActive: false,
        },
        {
          title: "用户dsd_a64_s",
          value: "鹅鹅鹅，曲项向天歌！",
          isActive: false,
        },
        { 
          title: "用户a64_das",
          value: "悄悄地我来了", 
          isActive: false
        },
      ],
      isActive: false,
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    remove(index) {
      
      this.messagelist.splice(index, 1);
    },
    displaybox(item) {
      
      if(item.isActive){
        return;
      }

      for(let i = 0; i< this.messagelist.length;i++){
        if (this.messagelist[i].isActive) {
              this.messagelist[i].isActive = false;
              break;
            }
      }
       item.isActive = true;


    },
  },
};
</script>

<style lang="scss" scoped>
.message {
  .textbox {
    .btn {
      background-color: #49dda1;
      list-style: none;
      border: none;
      padding: 10px;
      color: #fff;
      float: right;
    }

    .inp {
      height: 28px;
      width: 326px;
    }
    &.active {
      display: block;
    }
  }

  /deep/.van-nav-bar__text {
    color: #49dda1;
  }
  /deep/.van-nav-bar .van-icon {
    color: #49dda1;
  }
  .van-card__thumb {
    width: 68px;
    height: 68px;
  }
  .van-card__content {
    min-height: 66px;
  }
  .box {
    margin-top: 46px;
  }
  .van-card__desc {
    margin-top: 14px;
  }
  .delete-button {
    height: 100%;
  }
}
</style>